@import '../scss/breakpoints.scss';

* {
  font-family: Lexend;
  font-size: 16px;
  box-sizing: border-box;
}

p, a {
  font-family: Lexend;
  font-size: 16px;
  letter-spacing: 0px;

  @media (max-width: 425px) {
    font-size: 14px;
  }
}

@include to-xs {
  * {
    font-size: 14px;
  }
}


html {
  scroll-behavior: smooth; // Added for smooth back to top scroll
  height: 100%;
}

body {
  color: var(--text-primary-color);
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-size: 1.5rem;
  line-height: calc(1 + 0.8 / var(--font-body-scale));
  font-family: var(--font-body-family);
  font-style: var(--font-body-style);
  font-weight: var(--font-body-weight);
  &.fixed {
    overflow: hidden;
  }

  // @media screen and (min-width: 750px) {
  //   font-size: 1.6rem;
  // }
}

@include from-sm() {
  body {
    font-size: 1.6rem;
  }
}

main#MainContent {
  flex: 1;
}

// Global Link Styles
a {
  text-decoration: none;
  color: var(--text-primary-color);

  &:hover {
    color: var(--primary-color);
  }
}

h1,
h2,
h3,
h4,
h5,
.heading-sm,
.heading-md,
.heading-lg {
  font-family: Fira Sans Extra Condensed;
  font-style: normal;
  font-weight: 700;
  word-break: initial;
  margin: 0;
  em, strong, i, b {
    font-family: Fira Sans Extra Condensed;
  }
}

@include to-xs() {
  .heading-lg {
    font-size: var(--heading-lg-mobile);
    em, strong, i, b {
      font-size: var(--heading-lg-mobile);
    }
  }
}

@include from-xs() {
  .heading-lg {
    font-size: var(--heading-lg-tablet);
    em, strong, i, b {
      font-size: var(--heading-lg-tablet);
    }
  }
}

@include from-lg() {
  .heading-lg {
    font-size: var(--heading-lg);
    em, strong, i, b {
      font-size: var(--heading-lg);
    }
  }
}

