  height: 32px;
}

#cookieNotice {
  position: fixed;
  padding: 10px 15px;
  border-radius: 10px;
  box-shadow: 0 6px 6px rgb(0 0 0 / 25%);
  z-index: 999997;
  background-color: #ffffff;

