    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
  }

  &.display-right {
    position: fixed;
    left: 50%;
    bottom: 10px;
    transform: translateX(-50%);
    width: 35vw;

    // @media (max-width: 768px) {
    //   width: 85vw;
    // }
  }

  .title-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
    font-size: 20px;
  }

  p,a,ul {
    font-size: 12px;
    margin-bottom: 10px;
    text-align: center;
  }

  .btn-wrap {
    display: flex;
    flex-direction: row;
    font-weight: 700;
    justify-content: center;
    margin: 0 -5px 0 -5px;
    flex-wrap: wrap;

    button {
      flex-grow: 1;
      padding: 0 7px;
      margin: 0 5px 5px 5px;
      border-radius: 20px;
      line-height: 30px;
      cursor: pointer;
      white-space: nowrap;
