// animated check mark
.check-circle-outer, .spinner-circle-outer {
  --check-color: var(--accent-2-color);
  --circle-size: 50px;
  --check-width: calc( var(--circle-size) / 20 );

  width: var(--circle-size);
  height: var(--circle-size);
  position: relative;
  margin: auto;
  border-radius: 50%;
  overflow: hidden;


  .check-line-short {
    position: absolute;
    width: var(--check-width);
    height: 30%;
    background-color: var(--check-color);
    border-radius: 100px;
    rotate: -45deg;
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
    animation: enter 400ms ease 450ms forwards;
  }
  .check-line-long {
    --height: 60%;
    position: absolute;
    width: var(--check-width);
    height: 0;
    background-color:var(--check-color);
    border-radius: 100px;
    rotate: 45deg;
    top: 71%;
    left: 39%;
    transform: translate(-50%, -50%);
    animation: grow-line 400ms  cubic-bezier(0.68, -0.55, 0.27, 1.55) 650ms forwards;
  }

  .check-circle, .spinner-circle  {
    position: absolute;
    inset: 0;
    border-radius: 50%;
    border: var(--check-width) solid;
    border-color: var(--check-color);
    opacity: 0.2;

  }

  .check-circle-mask, .spinner-circle-mask {
    position: absolute;
    inset: 0;
    border-radius: 50%;
    border: var(--check-width) transparent solid;
    border-left-color: var(--check-color);
    opacity: 0;

  }
  .check-circle{
    animation: show 300ms ease 950ms forwards;
  }

  .check-circle-mask {
    --spin-end: 70deg;
    animation: spin-circle 300ms ease  350ms forwards;
  }
  .spinner-circle-mask {
    --spin-end: 0deg;
    animation: spin-circle 1000ms ease  forwards infinite;
  }

}
// animate cross //
.cross-circle-outer {
  --cross-color: #d8056b;
  --cross-size: 50px;
  --cross-width: calc(var(--cross-size) / 20);

  width: var(--cross-size);
  height: var(--cross-size);
  position: relative;
  margin: auto;
  border-radius: 50%;
  overflow: hidden;
  animation: shake 350ms ease-in-out 900ms;
}

.cross-line-left,
.cross-line-right {
  position: absolute;
  width: var(--cross-width);
  height: 0%;
  background-color: var(--cross-color);
  border-radius: 100px;
  top: 48%;
  left: 48%;
  animation: cross-grow 600ms cubic-bezier(0.68, -0.55, 0.27, 1.55) 250ms forwards;
}
.cross-line-right {
  rotate: 45deg;
}
.cross-line-left {
  rotate: -45deg;
}

.cross-circle {
  position: absolute;
  inset: 0;
  border-radius: 50%;
  border: var(--cross-width) solid;
  border-color: var(--cross-color);
  opacity: 0.2;
  animation: show 300ms ease 600ms forwards;
}

.cross-circle-mask {
  position: absolute;
  inset: 0;
  border-radius: 50%;
  border: var(--cross-width) transparent solid;
  border-left-color: var(--cross-color);
  animation: spin-circle 300ms ease forwards;
}

@keyframes spin-circle {
  0% {rotate: 320deg;
      opacity: 1;
  }

  99% {
      rotate: var(--spin-end);
      opacity: 1;
  }
  100%{
      opacity: 0;
  }
}
@keyframes enter {
  0% {
      left: 0%;
      top: 0%;
      }
  100% {
      left: 42%;
      top: 57%;
  }

}
@keyframes grow-line {
  0% {
      height: 0;
      top: 72%;
      left: 40%;
      }
  100% {

      height: var(--height);
      top: 44%;
      left: 40%;
  }
}

@keyframes show {
  to { opacity: 1;}
}

@keyframes show-message{
  from{ transform: translateY(30px);}
  to { opacity: 1;transform: translateY(0px)}
}

@keyframes cross-grow {
  0% {
    top: 48%;
    left: 48%;
    height: 0;
  }
  100% {
    top: 21%;
    left: 49%;
    height: 60%;
  }
}
@keyframes shake {
  0% {
    transform: translate(2px, 2px) rotate(0deg);
  }
  10% {
    transform: translate(-2px, -3px) rotate(-2deg);
  }
  20% {
    transform: translate(-5px, 0px) rotate(2deg);
  }
  30% {
    transform: translate(5px, 3px) rotate(0deg);
  }
  40% {
    transform: translate(2px, -2px) rotate(2deg);
  }
  50% {
    transform: translate(-2px, 3px) rotate(-2deg);
  }
  60% {
    transform: translate(-5px, 2px) rotate(0deg);
  }
  70% {
    transform: translate(5px, 2px) rotate(-2deg);
  }
  80% {
    transform: translate(-2px, -2px) rotate(2deg);
  }
  90% {
    transform: translate(2px, 3px) rotate(0deg);
  }
  100% {
    transform: translate(2px, -3px) rotate(-2deg);
  }
}
// LOADER

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid var(--accent-1-color); /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

